import React from "react"
import { Box, Link, ListItem, List, Heading } from "@chakra-ui/react"

const Toc = ({ data, location }) => {
  const list = data.headings

  return (
    <Box>
      <List>
        <Heading fontSize={24} fontWeight={700} pb={2.5}>
          Table of contents
        </Heading>
        {list
          .filter(item => item.depth > 1)
          .map(item => (
            <ListItem key={item.id} color="rlGrey" mb={item.depth > 2 ? 1 : 3}>
              <Link
                href={`${"#"}${item.id}`}
                ml={item.depth > 2 ? 4 : 0}
                color={location.hash === `${"#"}${item.id}` ? "rlBlue" : null}
              >
                {item.value}
              </Link>
            </ListItem>
          ))}
      </List>
    </Box>
  )
}

export default Toc
